import iHeader from "@/components/header/header-r"
import {Toast} from 'mint-ui'
    export default{
		name:"set",
		data(){
			return{
            show:true,
            title:"我的银行卡",
            content:"",
            users: localStorage.getItem('users') ? JSON.parse(localStorage.getItem('users')) : '',
            fullname:'',
            bankcard:'',
            bankbranch:'',
            bankbard:'',
            banklist:[],
            selectvalue:'',
            type_content:'',
            tousutype:'',
			type_list: [
			        {
			          flex:1,
			          values: [{link_value:'选择银行卡'}],
			          className: 'slot1',
			          textAlign: 'center'
			        }
            ],
            type_popup:false,
            getcoded:true,
            second:60,
            code:'',
            tips:true,
			}
		},components: {
            iHeader
        },methods:{
            is_fh(){
				this.type_popup=false;
            },	
            onTypeChange(picker, val){
				this.type_content = val[0].link_value;
				this.tousutype=val[0].link_name;
            },
            addBankcard(){
                if(!this.fullname){
                    Toast('请输入姓名')
                    return false;
                }
                 if(!this.tousutype){
                    Toast('请选择银行卡')
                    return false;
                }
                if(!this.bankbranch){
                    Toast('请填写开银行支行')
                    return false;
                } 
             /*    if(!(this.bankbard).match(/^\d{19}$/g)){
                    Toast('银行卡号填写错误')
                    return false;
                } */
                 if(!this.bankbard){
                    Toast('请填写银行卡号')
                    return false;
                }
                if(!this.code){
                    Toast('请填写验证码')
                    return false;
                }
                
                        this.$axios.post('/api/Usercenter/add_bank',`data=${JSON.stringify({
                                session_id:this.users.session_id,
                                userId:this.users.userId,
                                fullname:this.fullname,
                                branch:this.bankbranch,
                                bank:this.tousutype,
                                code:this.code,
                                bankbard:this.bankbard,
                                mobile:this.users.mobile,
                            })}`).then((res)=>{console.log(JSON.stringify(res))
                                    if(res.code==200){
                                        // console.log(res)
                                        // Toast(res.msg);
                                        setTimeout(() => {
                                        this.$router.push({path:'/my/my-bank'})
                                        }, 2000);
                                    }
                                    else if(res.code==999){
                                        Toast(res.msg)
                                    }
                            }).catch(err=>{})
            },
            getcode(){
                 this.$axios.post('/api/Privilege/get_code',`data=${JSON.stringify({
                                session_id:this.users.session_id,
                                userId:this.users.userId,
                                mobile:this.users.mobile,
                                type:'BINDBANK'
                            })}`).then((res)=>{console.log(JSON.stringify(res))
                                    if(res.code==200){
                                        console.log(res.data.limit_time)
                                        this.second=res.data.limit_time;
                                        this.getcoded=false;
                                        var timEnd=Number(res&&res.data.limit_time);
                                        console.log(timEnd)
                                        var _this=this;
                                        setTimeout(() => {
                                            _this.getcoded=true;
                                        }, timEnd*1000);
                                        setInterval(()=>{
                                            _this.second--;
                                            if(_this.second<=0){
                                                _this.second=timEnd;
                                            }
                                        },1000)
                                    }
                                    else if(res.code==999){
                                        Toast(res.msg)
                                    }
                            }).catch(err=>{})
            }
        },
        mounted(){
            //   console.log(this.users)
            
    
        },
        created(){
             this.$axios.post('/api/Usercenter/get_bank_list').then(
                    (res)=>{
                        console.log(JSON.stringify(res))
                        if(res.code==200){
                            // console.log(res)
                            var bankli=[];
                            var bankval={};
                                for(var i in Object.keys(res.data)){
                                         bankval.link_name=Object.keys(res.data)[i];
                                            this.type_list[0].values.push({link_name:Object.keys(res.data)[i],link_value:Object.values(res.data)[i]})
                                }
                                         console.log(this.type_list[0].values)
                        }
                        else if(res.code==999){
                            Toast(res.msg)
                        }
                }).catch(err=>{});
            var _this=this;    
            setTimeout(function(){_this.tips=false;},3000)
                
        }
    }



